import * as React from 'react';
import { Col, Table, Media } from 'reactstrap';

import CardDescription from './../Assets/CardDescription.png';
import CharacterDescription from './../Assets/CharacterDescription.png';
import ClockDescription from './../Assets/ClockDescription.png';
import SpecialDiceSlumber from './../Assets/SpecialDiceSlumber.png';
import SpecialDiceLucidity from './../Assets/SpecialDiceLucidity.png';
import SpecialProvidenceReq from './../Assets/SpecialProvidenceReq.png';

import Tile from './../Assets/Tile.png';
import TileBlessed from './../Assets/TileBlessed.png';
import TileCursed from './../Assets/TileCursed.png';
import TileDream from './../Assets/TileDream.png';
import TileMystery from './../Assets/TileMystery.png';
import TileShortcut from './../Assets/TileShortcut.png';
import TileStart from './../Assets/TileStart.png';

import Memory from './../Assets/Memory.png';
import Slumber from './../Assets/Slumber.png';
import Lucidity from './../Assets/Lucidity.png';
import Providence from './../Assets/Providence.png';

import MemoryIncrToken from './../Assets/MemoryIncrToken.png';
import MemoryDecrToken from './../Assets/MemoryDecrToken.png';
import ProvidenceToken from './../Assets/ProvidenceToken.png';

export default class Rules extends React.Component {
    public render = () => [
        <Col xs="12">
            <div className="rules-section-title">1. Welcome information</div>
        </Col>,
        <Col xs="12">Dream-Quest is a competitive game for 2-5 players, playable in around two hours. The players assume the role of a dreamer, existing in two different realities at once – the Dream World and the Real World. The Real World represents the repetitive routine of everyday life, that the characters have to endure before going to sleep and continuing their epic quest in the alternate reality.</Col>,
        <Col xs="12">
            <div className="rules-section-title">2. Objective of the game</div>
        </Col>,
        <Col xs="12">The players are racing to complete an epic quest and achieve complete physical transition into the Dream World by fulfilling a legendary deed (like beating a nightmare monster), which represents the only escape from the boring gray of everyday routine.</Col>,
        <Col xs="12">
            <div className="rules-section-title">3. Components, preparation</div>
        </Col>,
        <Col xs="6">
            <dl>
                <dd><span className="rules-bullet-point">1</span>Game Board</dd>
                <dd><span className="rules-bullet-point">10</span>Player Tokens</dd>
                <dd><span className="rules-bullet-point">45</span>Event Cards</dd>
                <dl>
                    <dd><span className="rules-bullet-point">35</span>Dream Event Cards</dd>
                    <dd><span className="rules-bullet-point">21</span>Real World Event Cards</dd>
                    <dd><span className="rules-bullet-point">1</span>Final Quest Card</dd>
                </dl>
            </dl>
        </Col>,
        <Col xs="6">
            <dl>
                <dd><span className="rules-bullet-point">6</span>Character Sheets</dd>
                <dd><span className="rules-bullet-point">2</span>Dice</dd>
                <dd><span className="rules-bullet-point">5</span>Lucidity sliders</dd>
                <dd><span className="rules-bullet-point">80</span>Providence Tokens</dd>
                <dd><span className="rules-bullet-point">30</span>Memory modifier tokens</dd>
                <dd><span className="rules-bullet-point">6</span>Slumber clocks and hands</dd>
            </dl>
        </Col>,
        <Col xs="12">
            <div className="rules-section-title">4. Component overview</div>
        </Col>,
        <Col xs="12">
            <div className="rules-section-subtitle">GameBoards</div>
            <div>The game board is divided into two worlds where the adventure can take place. The real world is divided into twelve spaces representing the flow of time. The dream world board depicts the mysterious land of dreams.</div>
        </Col>,
        <Col xs="12">
            <div className="rules-section-subtitle">Real World board</div>
            <div>This board is divided into twelve spaces. It serves to represent the background to the real adventure - the everyday reality of the player characters.</div>
            <div className="rules-section-subtitle">Dream Board</div>
            <div>The Dream Board is composed of normal and special spaces that the players can move across. The white spaces have no special effects associated with them, while the colored spaces provide additional gaming effects for players located on them.</div>
        </Col>,
        <Col xs="6">
            <Media className="media-info">
                <Media left><img src={TileStart} className="media-image" /></Media>
                <Media body>
                    <Media heading>Starting space</Media>
                    All characters start here. Some game rules may refer to this space.
                </Media>
            </Media>
            <Media className="media-info">
                <Media left><img src={TileDream} className="media-image" /></Media>
                <Media body>
                    <Media heading>The Dream Cycle</Media>
                    In order to win, the player's active avatar must be located on one of these spaces. You may NOT gain Providence in any way in the Dream Cycle.
                </Media>
            </Media>
            <Media className="media-info">
                <Media left><img src={TileCursed} className="media-image" /></Media>
                <Media body>
                    <Media heading>Cursed Areas</Media>
                    All drawn event cards have their Movement value reduced by 1 while the player avatar is on a cursed space.
                </Media>
            </Media>
            <Media className="media-info">
                <Media left><img src={TileBlessed} className="media-image" /></Media>
                <Media body>
                    <Media heading>Blessed Areas</Media>
                    Every time a player's avatar ends their turn on a blessed space, the player gains one Providence.
                </Media>
            </Media>
        </Col>,
        <Col xs="6">
            <Media className="media-info">
                <Media left><img src={TileMystery} className="media-image" /></Media>
                <Media body>
                    <Media heading>Mystery Areas</Media>
                    <div>At the beginning of a turn in which the player's avatar is active and located on a mystery space, roll a dice.</div>
                    <div>On a roll of 1-2, all drawn event cards have their movement value reduced to 0.</div>
                    <div>On a roll of 3-4, take your turn as normal.</div>
                    <div>On a roll of 5-6, All Lucidity rolls the player takes this turn are automatically successful.</div>
                </Media>
            </Media>
            <Media className="media-info">
                <Media left><img src={TileShortcut} className="media-image" /></Media>
                <Media body>
                    <Media heading>Shortcut</Media>
                    These shortcuts always indicate which route is the secret route (as denoted by the small arrow). The player can always choose one of the other paths, but can ONLY take the secret route if they began their turn on the crossroads space.
                </Media>
            </Media>
        </Col>,
        <Col xs="12">
            <div className="rules-section-subtitle rules-page-break">Cards</div>
            <img src={CardDescription} className="full-width-image" />
            <div>Additional examples:</div>
            <ul>
                <li><img src={SpecialDiceSlumber} /> - This reward will graint you Slumber equal to number you get by rolling the dice.</li>
                <li><img src={SpecialDiceLucidity} /> - This requirement is met only if you roll a number lower than your actual Lucidity.</li>
                <li><img src={SpecialProvidenceReq} /> - You can chose the coresponding option only if you have 5 or more Providence (you don't lose the Providence required).</li>
            </ul>
        </Col>,
        <Col xs="12">
            <div className="rules-section-subtitle">Character Sheet</div>
            <img src={CharacterDescription} className="full-width-image" />
        </Col>,
        <Col xs="12">
            <div className="rules-section-subtitle rules-page-break">Slumber Clock</div>
            <img src={ClockDescription} className="full-width-image" />
            <div>Slumber of each player is represented by a clock hand and a clock body. Always rotate the clock hand to point at your current slumber value.</div>
        </Col>,
        <Col xs="12">
            <div className="rules-section-title">5. Game Setup</div>
        </Col>,
        <Col xs="12">Set up the twin game board on the table. Set up the card decks in separate piles. Each player then takes two figurines of the same color. One is set up upwards in the real world (this is the active avatar), and the other is set up laterally in the Dreamlands (this is the inactive avatar).</Col>,
        <Col xs="12">Set up the chosen quest card next to the board. (This can be chosen randomly or agreed upon.)</Col>,
        <Col xs="12">Each player then takes one Lucidity Slider and sets it up on their character sheet. Likewise, each player takes sets their Slumber value to the starting amount indicated on their character sheet.</Col>,
        <Col xs="12">
            <div className="rules-section-title">6. Game Round</div>
        </Col>,
        <Col xs="12">During a game round, each player takes one player turn.</Col>,
        <div className="col-6">
            <div className="rules-section-subtitle">If player's Real Avatar is active</div>
            <div>The player takes a real world action:</div>
            <ol>
                <li>Move up one space in clockwise direction</li>
                <li>Draw a card from the Real Events Deck<div>This card will most likely impose a penalty on the player's next journey. Take the tokens necessary to represent this, or lower your current attribute value, or else keep the card next to your character sheet to represent this. Note that these penalties are one-time only, and are discarded after the character first Falls Asleep and their effects are activated.</div></li>
                <li>Fall Asleep</li>
            </ol>
        </div>,
        <div className="col-6">
            <div className="rules-section-subtitle">If player's Real Dream is active</div>
            <div>The player takes a dream world action:</div>
            <ol>
                <li>Check for Victory conditions</li>
                <li>Draw a card from the Dream Events deck</li>
                <li>Resolve the drawn card</li>
                <li>Move as many spaces as is the movement value indicated on the card (along with any movement modifiers imposed by the card effect). This movement must be in one direction, i.e. you can't return to a space that you were on earlier this turn. Note that this can be zero, in which case you don't move at all this round.</li>
                <li>Lose one Slumber.</li>
                <li>Suffer additional penalties or gain bonuses depending on the color of the dream region you are currently in.</li>
                <li>If the player's Slumber value is 0, immediately Wake Up.</li>
            </ol>
        </div>,
        <Col xs="12">After all the players have taken one player turn (in a clockwise direction), a new game round begins.</Col>,
        <Col xs="12">
            <div className="rules-section-title">7. Rules</div>
        </Col>,
        <Col xs="12">
            <div className="rules-section-subtitle">Waking Up and Falling Asleep</div>
            <dl>
                <dt><span className="rules-bullet-point">i.)</span>Waking Up</dt>
                <dd>When a player Wakes Up, reset their Lucidity and Slumber values to the number printed on their respective character sheet and remove all memory modifier tokens. Set the player's dream avatar as Inactive and the player's real avatar as Active. Note that the player retains any accumulated Providence tokens.</dd>
                <dt><span className="rules-bullet-point rules-page-break">ii.)</span>Falling Asleep</dt>
                <dd>When a player Falls Asleep, set their dream avatar as Active and their real avatar as Inactive. Then, roll a die to see if the character can remember the events of last night properly.</dd>
                <dd>Roll a die and consult the table below. The table shows how many spaces the player has to move BACK (on the shortest route towards the starting dream position - when in doubt, choose randomly).</dd>
                <dd></dd>
                <Table size="sm" borderless striped>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Rolled 6</th>
                            <th>Rolled 5</th>
                            <th>Rolled 4</th>
                            <th>Rolled 3</th>
                            <th>Rolled 2</th>
                            <th>Rolled 1</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <th scope="row">6 memory</th>
                            <td>1 spaces</td>
                            <td>0 spaces</td>
                            <td>0 spaces</td>
                            <td>0 spaces</td>
                            <td>0 spaces</td>
                            <td>0 spaces</td>
                        </tr>
                        <tr>
                            <th scope="row">5 memory</th>
                            <td>1 spaces</td>
                            <td>1 spaces</td>
                            <td>0 spaces</td>
                            <td>0 spaces</td>
                            <td>0 spaces</td>
                            <td>0 spaces</td>
                        </tr>
                        <tr>
                            <th scope="row">4 memory</th>
                            <td>2 spaces</td>
                            <td>1 spaces</td>
                            <td>1 spaces</td>
                            <td>0 spaces</td>
                            <td>0 spaces</td>
                            <td>0 spaces</td>
                        </tr>
                        <tr>
                            <th scope="row">3 memory</th>
                            <td>3 spaces</td>
                            <td>2 spaces</td>
                            <td>1 spaces</td>
                            <td>1 spaces</td>
                            <td>0 spaces</td>
                            <td>0 spaces</td>
                        </tr>
                        <tr>
                            <th scope="row">2 memory</th>
                            <td>3 spaces</td>
                            <td>3 spaces</td>
                            <td>2 spaces</td>
                            <td>1 spaces</td>
                            <td>1 spaces</td>
                            <td>0 spaces</td>
                        </tr>
                        <tr>
                            <th scope="row">1 memory</th>
                            <td>4 spaces</td>
                            <td>3 spaces</td>
                            <td>3 spaces</td>
                            <td>2 spaces</td>
                            <td>1 spaces</td>
                            <td>1 spaces</td>
                        </tr>
                    </tbody>
                </Table>
            </dl>
            <div className="rules-section-subtitle">Skills</div>
            <div>There are four different player statistics in Dream-Quest. These are:</div>
            <Media className="media-info">
                <Media left><img src={Memory} className="media-image" /></Media>
                <Media body>
                    <Media heading>Memory</Media>
                    <div>Represents how well a character can recall their dream journey. Used mostly to determine where the character will appear in the dream world when they wake up. Also sometimes used in dream events resolution. After player recevies any effect affecting his memory (f.e. real card effect) he should take the memory tokens to represent his current memory until the effect ends.</div>
                    <strong>Related items:</strong>
                    <ul>
                        <li><img src={MemoryIncrToken} /> - This token increases owners memory by 1.</li>
                        <li><img src={MemoryDecrToken} /> - This token decreases owners memory by 1.</li>
                    </ul>
                </Media>
            </Media>
            <Media className="media-info">
                <Media left><img src={Slumber} className="media-image" /></Media>
                <Media body>
                    <Media heading>Slumber</Media>
                    Represents how long the character can remain asleep. Stressful events and sleep deprivation all contribute to a reduced Slumber rate. All time spent in the dream world reduces the character's current slumber.
                </Media>
            </Media>
            <Media className="media-info">
                <Media left><img src={Lucidity} className="media-image" /></Media>
                <Media body>
                    <Media heading>Lucidity</Media>
                    <div>Represents how much control the character can exercise over the dream realms. Lucidity is used to determine the outcome of encounters drawn from the dream encounters deck. The players may also attempt to wake up other players using lucidity (provided they are on the same space as the other character).</div>
                    <div>Note that the maximum Lucidity value a character can have is 6.</div>
                    <dl>
                        <dt>Waking up other players</dt>
                        <dd>
                            <div>If you end your movement on a space comtaining another player's avatar, you may attempt to wake them up. If you do, you both roll a die and add your current lucidity to the roll. If you win the roll-off, the enemy loses Slumber points equal to the difference between the two rolls. You then move your avatar one space forward and move the enemy's avatar one space back (you choose which way to move them).</div>
                            <div>If you stop your movement on the space containing another player's inactive avatar, do not roll-off, instead just move them one space back.</div>
                            <div>There is no such player interaction in the real world. The characters might meet, but their interactions are ultimately as meaningless as their existence.</div>
                            <div>Note that if a player is moved out of their turn like this, the effects of the space they moved to (like Blessed Spaces) is not applied.</div>
                        </dd>
                    </dl>
                </Media>
            </Media>
            <Media className="media-info">
                <Media left><img src={Providence} className="media-image" /></Media>
                <Media body>
                    <Media heading>Providence</Media>
                    <div>Represents the knowledge the character has accumulated during their journey. Providence is amassed during play and is used during the final encounter.</div>
                    <strong>Related items:</strong>
                    <ul>
                        <li><img src={ProvidenceToken} /> - A token worth 1 Providence.</li>
                    </ul>
                </Media>
            </Media>
            <div className="rules-section-subtitle rules-page-break">Active vs Inactive avatars</div>
            <div>At all times, each player must have two avatars: one situated on the Real World board, and the other on the Dream board. One of these avatars is to be "active", i.e. set up upright (see picture) and the other "inactive", i.e. set up laterally (see picture). The player can only move the active avatar. If a player should find themself to have two active avatars, or two inactive avatars, activate the Real World avatar and deactivate the Dream World avatar.</div>
            <div className="rules-section-subtitle">Use of Providence</div>
            <div>Providence can be spent to gain temporary bonuses to rolls, movement, etc. The complete list of what a player can use providence for is as follows:</div>
            <Table size="sm" borderless striped>
                <thead><tr><th>Effect</th><th>Cost</th></tr></thead>
                <tbody>
                    <tr><td>Re-roll 1 dice</td><td>5</td></tr>
                    <tr><td>Add 1 to dice roll</td><td>5</td></tr>
                    <tr><td>Immediately move 1 space</td><td>5</td></tr>
                    <tr><td>Increase Slumber value by 1</td><td>3</td></tr>
                </tbody>
            </Table>
            <div className="rules-section-subtitle">Rolling</div>
            <div>Some options in the Dream World require a player to make a lucidity roll or a memory roll. In such cases, the player has to rull UNDER their current value. If they roll the same as their characteristic or higher, the result is a failure. However, an unmodified roll of a natural 1 is always a success.</div>
            <div className="rules-section-subtitle">Playing a Dream Event Card</div>
            <div>Dream Event cards are played in several steps:</div>
            <dl>
                <dt><span className="rules-bullet-point">i.)</span>Choose one of the options listed on the card. These can include bonuses, penalties, or requirements.</dt>
                <dt><span className="rules-bullet-point">ii.)</span>Execute the chosen option, applying any bonuses or penalties imposed.</dt>
                <dd>If you have chosen an option that includes a requirement, you need to FIRST pay the price listed in the requirements, then do the roll (if there is one) and then, depending on whether or not the requirements were met, you either take the success or the failure outcome. Note that if you get woken up by the requirements, you don't even get to roll, and you immediately Wake Up, discarding the card. Similarly, if the outcome of the encounter wakes you up, you don't get to move.</dd>
                <dt><span className="rules-bullet-point">iii.)</span>Look at the movement value on the card, along with any modifierd that your chosen option may have imposed. This is the movement value used in the next phase, movement.</dt>
            </dl>
            <div>You can often choose between trying your luck and paying attention to the encounter, or ignoring it and continuing on your journey. Sometimes, requirements may be in the form of (7+P). To be successful when choosing this option, your current Providence value must be at least seven.</div>
        </Col>,
        <Col xs="12">
            <div className="rules-section-title">8. Ending the game</div>
        </Col>,
        <Col xs="12">
            <div>The game ends once a player is victorious. To win, a player must fulfill the conditions shown on the current Quest card.</div>
            <div>If these conditions are simultaneously met, the player is victorious.</div>
        </Col>,
    ]
}
